export function safeParseJson(value) {
    try {
        if (value) {
            const content = value.trim();
            return (content !== "")
                ? JSON.parse(content)
                : null;
        }
    }
    catch (e) {
        console.error(`Could not parse JSON content: ${e.message}`, e);
    }
    return null;
}
export function parseElementAsJson(element) {
    return safeParseJson((element.textContent || "")
        .replace(/&lt;/g, "<")
        .replace(/&gt;/g, ">")
        .replace(/&amp;/g, "&"));
}
